import React from "react";
import { useEffect } from "react";
import { useState } from "react";


const ProfileStatusWithHooks = (props) => {

    const [
        [editMode, setEditMode],
        [status, setStatus]
    ] = [
            useState(false),
            useState(props.status)
        ]

    let activateEditMode = () => {

        setEditMode(true)
    }

    useEffect(()=>{
        setStatus(props.status);
    }, [props.status]);

    let deactivateEditMode = () => {

        setEditMode(false)

        props.updateStatus(status);

    }

    let setTextAreaValue = (e) => {
        let text = e.currentTarget.value;
        setStatus(text);
    }

    return (
        <>
            {!editMode &&

                <div>
                    <span onDoubleClick={activateEditMode}>{props.status || "Новый статус"}</span>
                </div>
            }
            {editMode &&

                <div>
                    <input onChange={setTextAreaValue} autoFocus={true} onBlur={deactivateEditMode} value={status || props.status} />
                </div>
            }

        </>
    )

}



export default ProfileStatusWithHooks