import { stopSubmit } from "redux-form";
import { authAPI } from "../api/api";

const SET_USER_DATA_AUTH = "samurai/auth/SET_USER_DATA_AUTH";


let inititalState = {

    id: null,
    email: null,
    login: null,
    isAuth: false,
}




const authReducer = (state = inititalState, action) => {
    // debugger;
    switch (action.type) {
        case SET_USER_DATA_AUTH:
            return {
                ...state,
                ...action.payload,
            }

        default:
            return { ...state };
    }
}




const setUserData = ({ id, email, login, isAuth }) => ({ type: SET_USER_DATA_AUTH, payload: { id, email, login, isAuth } });

export const getAuthUserData = () => async (dispatch) => {

    let response = await authAPI.me();
        
    response.data.resultCode === 0 && (dispatch(setUserData({ ...response.data.data, isAuth: true })));
    response.data.resultCode === 1 && (dispatch(setUserData({ id: null, email: null, login: null, isAuth: false })));

}

export const loginThunk = (email, password, rememberMe) => async (dispatch) => {

    const response = await authAPI.login(email, password, rememberMe);
    if (response.data.resultCode === 0) {
        dispatch(getAuthUserData());
    } else {
        dispatch(  stopSubmit("LoginForm", {_error: response.data.messages.join("<br>") }) );
    }
    
}



export const logoutThunk = () => async (dispatch) => {

    let response = await authAPI.logout();
    if (response.data.resultCode === 0) {
        dispatch(getAuthUserData());
    }

}





export default authReducer;