

import s from './Users.module.css';
import defaultUserPhoto from "../../assets/images/user.png"
import { NavLink } from 'react-router-dom';
import Paginator from '../Common/Paginator/Paginator';

const Users = (props) => {



    return (
        <div className={s.container}>

            <Paginator {...props} />

            {
                props.users.map(u => <div className={s.userBlock} key={u.id}>

                    <span>
                        <div>
                            <NavLink to={"/profile/" + u.id}>
                                <img alt="" className={s.userPhoto} src={u.photos?.small || defaultUserPhoto} />
                            </NavLink>
                        </div>
                        <div>
                            {u.followed
                                ? <button disabled={props.followinInProgess.includes(u.id)} onClick={() => props.unfollow(u.id)}>UnFollow</button>
                                : <button disabled={props.followinInProgess.includes(u.id)} onClick={() => props.follow(u.id)}>Follow</button>}
                        </div>
                    </span>
                    <div className={s.userInfo}>
                        <span>
                            <div>{u.id}</div> <div>{u.fullName}</div> <div>{u.status}</div>
                        </span>
                        <span>
                            <div>{"u.location?.city"}</div>
                            <div>{"u.location?.country"}</div>
                        </span>
                    </div>

                </div>
                )
            }
        </div>)
}

export default Users