import { Redirect, withRouter } from "react-router-dom";
import { Field, reduxForm } from "redux-form"
import { maxLengthCreator, requiredField } from "../../utils/validators";
import { Input } from "../Common/FormsControls/formsControls";
import s from "../Common/FormsControls/formsControl.module.css";


const Login = (props) => {

    const onSubmit = (obj) => {
        console.log(obj);

        props.loginThunk(obj.login, obj.password, obj.rememberMe);

    }


    if (props.isAuth) {
        return <Redirect to={props?.location?.state?.requestedPage || "/profile"} />
    }

    //


    return <div>
        <h1>Login</h1>
        <ReduxLoginForm onSubmit={onSubmit} />

    </div>

}




const maxLength50 = maxLengthCreator(50);
// 

const LoginForm = ({ handleSubmit, error }) => {

    debugger;
    return <form onSubmit={handleSubmit}>
        <div>
            <Field name="login" component={Input} validate={[requiredField, maxLength50]} placeholder={"login"} />
        </div>
        <div>
            <Field name="password" component="input" placeholder={"password"} />
        </div>
        <div>
            <Field name="rememberMe" component={"input"} type="checkbox" /> Remember me
        </div>
        {error && (<div className={s.formSummaryError}>

            {error}

        </div>)}

        <div>
            <button>Login</button>
        </div>
    </form>
}


const ReduxLoginForm = reduxForm({ form: "LoginForm" })(LoginForm);

export default withRouter(Login)