import { Suspense } from "react"
import Preloader from "../components/Common/Preloader/Preloader"

export default (WrappedComponent) => {


    return (props) => (
        <Suspense fallback={<Preloader />}>
            <WrappedComponent {...props} />
        </Suspense>)


}


export const Susp = ({ ...props }) => {
    //debugger;
    return (
        <Suspense fallback={<Preloader />}>
            {props.children}
        </Suspense>)
}

