import s from "./formsControl.module.css"



const FormControl = ({input, meta, ...props}) => {
//debugger;
    return (
        <div className={s.formControl + " " + (meta.invalid && meta.touched ? s.error : "")}>
          <div>
            {props.children}
          </div>

        <div>
            <span>{(meta.error && meta.touched) ? meta.error : ""}</span>
        </div>

        </div>
    )

}


export const Input = ({input, ...props}) => {

    return <FormControl {...props} ><input type="text" {...input} {...props} /></FormControl>


}



export const Textarea = ({input, ...props}) => {

    return <FormControl {...props} ><textarea {...input} {...props}></textarea></FormControl>


}