import { NavLink } from "react-router-dom";
import classes from "./Header.module.css"

const Header = (props) => {
    return (
        <header className={classes.header}>
          <img alt="" className={classes.logo} src="https://upload.wikimedia.org/wikipedia/commons/c/c4/CompSA.png" />
          <div className={classes.loginBlock}>
              {
                  props.isAuth ? <div><div>{props.login}</div><div><button onClick={props.logoutThunk}>logout</button></div></div> : <NavLink to={'/login'}>Login</NavLink>
              }
              
          </div>

      </header>
    )
}

export default Header;