import { usersAPI } from "../api/api";
import { updateObjectInArray } from "../utils/object-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_COUNT = 'SET_TOTAL_COUNT';

const TOGGLE_PRELOADER = 'TOGGLE_PRELOADER';
const TOGGLE_IS_FOLLOWING_PROGESS = 'TOGGLE_IS_FOLLOWING_PROGESS';




let inititalState = {
    users: [],
    totalCount: 0,
    pageSize: 5,
    currentPage: 1,
    isLoading: false,
    followinInProgess: []
};


const usersReducer = (state = inititalState, action) => {
    console.log(action.type)
    switch (action.type) {

        case TOGGLE_PRELOADER:
            return {
                ...state,
                isLoading: action.isLoading
            }

        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.newCurrentPage
            };

        case FOLLOW:
            var obj = {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id", { followed: true })
                //state.users.map(u => u.id === action.userId ? { ...u, followed: true } : u)
            };

            return obj

        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userId, "id", { followed: false })
            };

        case SET_TOTAL_COUNT:
            return {
                ...state,
                totalCount: action.totalCount
            }

        case SET_USERS:
            return {
                ...state,
                /// users: [...state.users, ...action.users ] 
                users: [...action.users]
            }

        case TOGGLE_IS_FOLLOWING_PROGESS:

            return {
                ...state,
                followinInProgess: action.isFetching ?
                    [...state.followinInProgess, action.followinInProgess] :
                    state.followinInProgess.filter(el => el !== action.followinInProgess)
            }

        default:
            return { ...state }

    }

}


export const followAC = (userId) => ({ type: FOLLOW, userId });
export const unfollowAC = (userId) => ({ type: UNFOLLOW, userId });
export const setUsers = (users) => ({ type: SET_USERS, users })
export const setCurrentPage = (newCurrentPage) => ({ type: SET_CURRENT_PAGE, newCurrentPage })
export const setTotalUsersCount = (totalCount) => ({ type: SET_TOTAL_COUNT, totalCount })
export const togglePreloader = (isLoading) => ({ type: TOGGLE_PRELOADER, isLoading })
export const toggleFollowinInProgess = (isFetching, followinInProgess) => ({ type: TOGGLE_IS_FOLLOWING_PROGESS, isFetching, followinInProgess })


export const getUsers = (pageSize, currentPage) => async (dispatch) => {

    dispatch(togglePreloader(true));

    dispatch(setCurrentPage(currentPage));

    const data = await usersAPI.getUsers(pageSize, currentPage);

    dispatch(setUsers(
        data.items.map(u => {
            return { ...u, fullName: u.name }
        })
    ));
    dispatch(setTotalUsersCount(data.totalCount));
    dispatch(togglePreloader(false));





}

const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {




    dispatch(toggleFollowinInProgess(true, userId));

    const data = await apiMethod(userId);
    if (data.resultCode === 0) {
        dispatch(actionCreator(userId));
    }

    dispatch(toggleFollowinInProgess(false, userId));


}

export const follow = (userId) => async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(usersAPI), followAC);
}


export const unfollow = (userId) => async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.unfollow.bind(usersAPI), unfollowAC);
}




export default usersReducer;