const MyPosts = (props) => {

   
  let onAddPost = (obj) => {
    props.addPost(obj.newPost);
  };

  return (
    <div className={classes.postBLock}>
      my Posts

      <Formik onSubmit={onAddPost} initialValues={{newPost:""}} validationSchema={MyPostSchema} >
        {({errors}) => (
        <Form>
          <div>
            <div>
              <Field component="textarea"  name="newPost" />
            </div>
            <div>
              {errors.newPost ? errors.newPost : null}
            </div>

            <div>
              <button>add Post </button>
            </div>
          </div>
          </Form>
        )}
      </Formik>

      <div className={classes.posts}>

        {props.postsElements}

      </div>
    </div>)
}


export default MyPosts