import { useState } from "react";
import s from "./Paginator.module.css";



const Paginator = ({ totalCount, pageSize, currentPage, onPageChanged, portionSize = 10 }) => {


    let pagesCount = Math.ceil(totalCount / pageSize);
    let pages = [];
    for (let index = 1; index <= pagesCount; index++) {
        pages.push(index);
    }


    let portions = [];

    let portionsCount = Math.ceil(pages.length / portionSize);




    const [currentPortion, setCurrentPortion] = useState(Math.ceil(currentPage / portionSize) - 1);

    const prev = () => {

        setCurrentPortion(Math.max(0, currentPortion - 1));
    }

    const next = () => {

        setCurrentPortion(Math.min(portionsCount, currentPortion + 1));
    }




    return (<div className={s.paginator}>
        <button onClick={prev}>Prev</button>
        {
            pages.filter(p => p >= currentPortion * portionSize && p < (currentPortion + 1) * portionSize).map(p =>
                <span key={p} onClick={() => { onPageChanged(p); }} className={currentPage === p ? s.selectedPage : ""} >{p}</span>
            )
        }
        <button onClick={next}>Next</button>
    </div>)
}



export default Paginator;