import React from "react";
import { connect } from "react-redux";
import { getUsers, follow, unfollow } from "../../redux/users-reducer";
import Preloader from "../Common/Preloader/Preloader";
import Users from "./Users";
import withAuthRedirect from "../../hoc/withAuthRedirect";
import { compose } from "redux";
import { getCurrentPage, getFollowinInProgess, getIsLoading, getPageSize, getTotalCount, getUsersSelector, getUsersSuperSelector } from "../../redux/users-selectors";



class UsersAPIComponent extends React.Component {


    componentDidMount() {

        this.props.getUsers(this.props.pageSize, this.props.currentPage);

    }

    onPageChanged = (pageNumber) => {

        this.props.getUsers(this.props.pageSize, pageNumber);

    }

    render() {

        return <>
            {this.props.isLoading && <Preloader />}
            <Users users={this.props.users}
                totalCount={this.props.totalCount}
                pageSize={this.props.pageSize}
                currentPage={this.props.currentPage}
                unfollow={this.props.unfollow}
                follow={this.props.follow}
                isLoading={this.props.isLoading}
                followinInProgess={this.props.followinInProgess}
                toggleFollowinInProgess={this.props.toggleFollowinInProgess}
                onPageChanged={this.onPageChanged} />
        </>
    }

}


