

const UPDATE_NEW_MESSAGE_BODY = "UPDATE-NEW-MESSAGE-BODY";
const ADD_MESSAGE = "ADD_MESSAGE";


let inititalState = {
    dialogs: [
      { id: "1", name: "Dimich" },
      { id: "2", name: "Alex" },
      { id: "3", name: "Sveta" },
      { id: "4", name: "Sasha" },
      { id: "5", name: "Viktor" },
      { id: "6", name: "Valera" },
    ],


    messages: [
      { id: "1", message: "Hello!", direction: "in" },
      { id: "2", message: "How are you!", direction: "in" },
      { id: "3", message: "Yo!", direction: "out" }
    ],

   // newMessageText: ""
  };


const dialogsReducer = (state = inititalState, action) => {

   
    switch (action.type) {

        case ADD_MESSAGE:
            return {
                ...state,
                messages : [...state.messages, 
                    {
                        id: state.messages.length + 1,
                        message: action.body,
                        direction: "out"
                    }
                ]
            };
        default:
            return {...state};
    }


}


export const addMessage = (body) => ({
    type: ADD_MESSAGE, body
});

export default dialogsReducer
