import { profileAPI, usersAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT';
const SET_USER_DATA = 'SET_USER_DATA';
const SET_USER_STATUS = 'SET_USER_STATUS';
const DELETE_POST = "DELETE_POST";

let inititalState = {

    posts: [
        { id: "1", message: "Hi, how are you?", likeCounts: 5 },
        { id: "2", message: "It's my first post", likeCounts: 15 },
    ],
    textAreaValue: "",

    userData: {},

    status: ""


};


const profileReducer = (state = inititalState, action) => {



    switch (action.type) {

        case SET_USER_DATA:

            return {
                ...state,
                userData: action.userData
            }

        case SET_USER_STATUS:

            return {
                ...state,
                status: action.status
            }


        case ADD_POST:

            return {
                ...state,
                posts: [...state.posts, {
                    id: 5,
                    message: action.newPost,
                    likeCounts: 0
                }],
               
            }

        case DELETE_POST:

            return {
                ...state,
                posts: state.posts.filter(el=>el.id !=action.postIndex ) 
               
            }


        
        default:

            return state;
    }
}


const setUserProfile = (profile) => ({
    type: SET_USER_DATA,
    userData: profile
});


const setUserStatus = (status) => ({
    type: SET_USER_STATUS,
    status
});



export const addPostActionCreator = (newPost) => ({
    type: ADD_POST, newPost
});

export const deletePost = (postIndex) => ({
    type: DELETE_POST, postIndex
});

export const getUserProfile = (userId) => async (dispatch) => {

    let data = await usersAPI.getUserProfile(userId);
    dispatch(setUserProfile(data));

}


export const getUserStatus = (userId) => async (dispatch) => {

    let data = await profileAPI.getStatus(userId);
    dispatch(setUserStatus(data));

}


export const updateStatus = (status) => async (dispatch) => {

    let data = await profileAPI.updateStatus(status);
    if (data.resultCode ===0) {
        dispatch(setUserStatus(status));
    }

}


export default profileReducer;