import classes from "./Post.module.css"


const Post = (props) => {
  return (
    <div className={classes.item}>
      <div>  <img alt="" src="https://i.ytimg.com/vi/1Ne1hqOXKKI/maxresdefault.jpg" />
        {props.message}
      </div>
      <span>like ({props.likeCounts})</span>
    </div>
  )
}


export default Post